/* ハンバーガーメニューが閉じているとき */
.header-nav {
  display: block;
  position: fixed;
  top: 0;
  right: -400px;
  bottom: 0;
  z-index: 3;
  transition: right 0.5s;
  font-size: 1rem;
  text-align: center;
  background-color: rgb(255, 255, 255);
}
@media screen and (min-width: 768px) {
  .header-nav {
    margin-left: auto;
    text-align: right;
    position: static;
    width: auto;
    height: auto;
    transform: translateX(0);
    display: block;
    background-color: transparent;
    transition: none;
  }
}

@media screen and (min-width: 768px) {
  .header-nav__list {
    display: flex;
    gap: 6px;
  }
}

.header-nav__item {
  font-size: 0.875rem;
  display: flex;
  align-items: center;
  position: relative;
  padding: 0 8px;
}
@media screen and (min-width: 768px) {
  .header-nav__item {
    font-size: 1rem;
  }
  .header-nav__item::after {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    width: 1px;
    height: 60%;
    background-color: rgba(0, 0, 0, 0.2);
  }
  .header-nav__item:last-child::after {
    display: none;
  }
}
.header-nav__item .header-nav__item-cart {
  font-size: 24px;
}

.header-nav__item-cart {
  border: 1px solid rgba(0, 0, 0, 0.5);
  background-color: rgba(66, 212, 245, 0.3);
  border-radius: 12px;
  padding: 2px 4px;
}
.header-nav__item-cart .header-nav__icon {
  width: 24px;
  height: 18px;
  object-fit: contain;
}
@media screen and (min-width: 768px) {
  .header-nav__item-cart .header-nav__icon {
    width: 24px;
    height: 18px;
    object-fit: contain;
  }
}

/* ハンバーガーメニューが開いているとき */
.open .header-inner {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 50px;
  opacity: 1;
}

.open .header-nav {
  right: 0;
  padding: 50px 25px;
}
@media screen and (min-width: 768px) {
  .open .header-nav {
    position: static;
    width: auto;
    height: auto;
    transform: translateX(0);
    display: block;
    background-color: transparent;
    font-size: 1rem;
    transition: none;
  }
}

.open .header-nav__item {
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
  padding-bottom: 8px;
  margin-bottom: 16px;
}
.open .header-nav__item:last-child {
  margin-bottom: 0;
}
@media screen and (min-width: 768px) {
  .open .header-nav__item {
    margin-top: 0;
  }
}

/*ハンバーガーメニュー三本線の配置*/
.hamburger-btn {
  width: 24px;
  height: 18px;
  display: flex;
  position: relative;
  justify-content: space-between;
  margin-left: auto;
  transition: all 0.5s;
  cursor: pointer;
  z-index: 4;
}
@media screen and (min-width: 768px) {
  .hamburger-btn {
    display: none;
  }
}

/* 三本線のバー(閉じているとき) */
.hamburger-btn__bar {
  position: absolute;
  width: 24px;
  height: 2px;
  background-color: #000000;
  transition: all 0.5s;
}
@media screen and (min-width: 768px) {
  .hamburger-btn__bar {
    display: none;
  }
}

/* 1~3本目の線の設定(閉じているとき) */
.hamburger-btn__bar:nth-child(1) {
  bottom: 16px;
}

.hamburger-btn__bar:nth-child(2) {
  bottom: 8px;
}

.hamburger-btn__bar:nth-child(3) {
  bottom: 0px;
}

/* 1~3本目の線の設定(開いているとき）*/
.open .hamburger-btn__bar:nth-child(1) {
  transform: translateY(8px) rotate(-45deg);
}

.open .hamburger-btn__bar:nth-child(2) {
  opacity: 0;
}

.open .hamburger-btn__bar:nth-child(3) {
  transform: translateY(-8px) rotate(45deg);
}

/* メニューが閉じていている時の背景スタイル */
#hamburger-mask {
  display: none;
  transition: all 0.5s;
}

/*メニューが開いている時の背景スタイル*/
.open #hamburger-mask {
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: gainsboro;
  opacity: 0.5;
  /*色の透明度*/
  cursor: pointer;
}
@media screen and (min-width: 768px) {
  .open #hamburger-mask {
    display: none;
  }
}

